<template>
   <div>
       <div id="header">
         <header-nav :nav="name" />
       </div>

       <div class="clearfix content padding-top-100">
         <h1>“{{keyword}}”相关的搜索结果</h1>
         <el-tabs class="mt20" v-model="activeName" @tab-click="handleClick">
           <el-tab-pane label="菜谱" name="1">
             <el-tabs class="mt20" v-model="childName" @tab-click="childClick">
               <el-tab-pane v-for="(item, index) in editableTabs"
                            :key="item.name"
                            :label="item.title"
                            :name="item.name">
                 <div class="mt30 mb30 text-center" v-if="menuList.length==0">
                   <el-avatar shape="square" :size="200" :fit="fit" :src="nullUrl" ></el-avatar>
                   <div>竟然搜索不到～换个词试试吧！</div>
                 </div>
                 <div v-for="(item, index) in menuList" :key="index" class="tjcp-box" v-else>
                   <div class="left tjcp-left">
                     <div class="food-img" :style="{ backgroundImage: 'url('+osshost + item.cpimg +detailpic+ ')' }"
                          @click="navToDetail(item.id)"></div>
                   </div>
                   <div class="left tjcp-right ml15">
                     <div class="mt10">
                       <span class="text-bold text-20 text-cut">{{ item.title }}</span>
                     </div>
                     <div class="mt10 f-user" @click="navToIndex(item.userid)">
                       <el-avatar class="f-user-img" :src="imgurl" v-if="!item.faceImg"></el-avatar>
                       <el-avatar class="f-user-img" :src="osshost+ item.faceImg + userface" v-if="item.faceImg&&item.isThree==false"></el-avatar>
                       <el-avatar class="f-user-img" :src="item.faceImg" v-if="item.faceImg&&item.isThree==true"></el-avatar>
                       <span class="text-14">{{ item.nickname }}</span>
                     </div>
                     <div class="grayColor mt10">
                       <span class="text-12">被收藏次数: {{ item.collections }}</span>
                     </div>
                   </div>
                 </div>
                 <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
                   <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
                   <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
                   <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
                 </infinite-loading>
               </el-tab-pane>
             </el-tabs>
           </el-tab-pane>
           <el-tab-pane label="用户" name="2">
             <div class="mt30 mb30 text-center" v-if="userList.length==0">
               <el-avatar shape="square" :size="200" :fit="fit" :src="nullUrl" ></el-avatar>
               <div>竟然搜索不到～换个词试试吧！</div>
             </div>
             <ul class="dr_list mt30" v-else>
               <li class="clearfix mb15" v-for="(item, index) in userList" :key="index">
                 <div class="left headicon bg-img br50" @click="navToIndex(item.id)"
                      :style="{backgroundImage:'url('+osshost+ item.faceimg +userface+')'}">
                   <div class="bg-img indefiy" :style="{ backgroundImage: 'url(../../static/indefiy.png)' }"
                        v-if="item.certificationstatus==1">
                   </div>
                 </div>
                 <div class="info mt10">
                   <h2 class="text-bold text-17">{{item.nickname}}</h2>
                 </div>
               </li>
             </ul>
             <infinite-loading @infinite="infiniteUser">
               <div slot="spinner">小弟拼命加载中...</div>//加载中的文字
               <div slot="no-more">已加载完毕!</div>//加载完毕的文字
               <div slot="no-results">暂无数据:(</div>//没有数据的文字
             </infinite-loading>
           </el-tab-pane>
         </el-tabs>

       </div>
     <FooterBottom></FooterBottom>
     <Totop></Totop>
   </div>
</template>

<script>
    import HeaderNav from "../components/headerNav";
    import FooterBottom from "@/components/footer";
    import Totop from '@/components/totop.vue';
    import {Message} from  "element-ui"
    import InfiniteLoading from 'vue-infinite-loading';
    import {searchMenuList,searchUserList}from "../api/search"
    export default {
        name: "search",
      components: {
          HeaderNav,
          FooterBottom,
          InfiniteLoading,
      Totop
      },
       data(){
          return{
            page:0,
            name:'search',
            osshost:process.env.UPLOAD_URL,
            nullUrl:'../../static/dataNull.png',
            listpic: '?x-oss-process=style/list-img',
            userface: '?x-oss-process=style/user-img',
            detailpic: '?x-oss-process=style/detail-img',
            activeName: '1',
            childName:'1',
            editableTabs: [{
              title: '综合最佳',
              name: '1',
            }, {
              title: '最多喜欢',
              name: '2',
            },{
              title: '最新发布',
              name: '3',
            }],
            title:'',
            keyword:'',
            menuList:[],
            userList:[],
            imgurl: '../../static/userIcon.png',
            isThree: false
          }
       },
      created() {
        this.keyword = this.$route.query.keyword;
      },
      methods:{
        handleClick(tab, event) {
          this.activeName = tab.name;
          this.page=0;
          if(tab.name=="1"){
            this.menuList = [];
            this.infiniteHandler()
          }else{
            this.userList = [];
            this.infiniteUser()
          }
        },
        childClick(tab, event) {
          this.childName = tab.name;
          this.page=0;
          this.menuList = [];
          this.infiniteHandler()
        },
        infiniteHandler($state){
          this.page++;
          this.getMenuList($state);
        },
        getMenuList($state){
          searchMenuList(this.keyword,this.childName,this.page).then(response=>{
            let res = response.data;
            if (res.success) {
              if (res.result.length != 0) {
                this.menuList = this.menuList.concat(res.result); //追加新数据
                for(var i=0;i<this.menuList.length;i++){
                	this.menuList[i].isThree = new RegExp('http').test(this.menuList[i].faceImg);
                };
                if(this.page!=0){
                  $state.loaded();
                }
              } else {
                $state.complete(); //这里是加载了10页数据，设置不在加载
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error=>{
            console.log(error)
          })
        },
        infiniteUser($state){
          this.page++;
          this.getUserList($state);
        },
        getUserList($state){
          searchUserList(this.keyword,this.page).then(response=>{
            let res = response.data;
            if (res.success) {
              if (res.result.length != 0) {
                this.userList = this.userList.concat(res.result); //追加新数据
                if(this.page!=0){
                  $state.loaded();
                }
              } else {
                $state.complete(); //这里是加载了10页数据，设置不在加载
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error=>{
            console.log(error)
          })
        },
        navToDetail(id) {
          this.$router.push({path: "/cookBook", query: {id: id}})
        },
        navToIndex(id) {
          this.$router.push({path: "/user", query: {id}})
        }
      }

    }
</script>

<style scoped>
  .el-avatar{
    margin: 0 auto;
    background-color: white;
  }
  .el-avatar>img{
    border: none;
  }
</style>



